.card__stars {
  display: flex;
}

.stars__star {
  background-image: url(../images/star.svg);
  height: 16px;
  width: 16px;
  background-repeat: no-repeat;
  background-position: center;

  &:not(:last-child) {
    margin-right: 4px;
  }
}

.card__stars--1 .stars__star:nth-child(-n+1),
.card__stars--2 .stars__star:nth-child(-n+2),
.card__stars--3 .stars__star:nth-child(-n+3),
.card__stars--4 .stars__star:nth-child(-n+4),
.card__stars--5 .stars__star:nth-child(-n+5)
{
  background-image: url(../images/star-active.svg);
}

