* {
  box-sizing: border-box;
}

html {
  color: #060b35;
  font-family: Roboto, sans-serif;
}

body, p, h2 {
  margin: 0;
}

.card {
  width: 200px;
  padding: 33px 17px 17px;
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__title {
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__review-count {
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  display: flex;
}

.card__price-name {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-sum {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buyButton {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  margin-top: 16px;
  padding: 12px 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: block;
}

.card__buyButton:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}

.card__stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.card__stars--1 .stars__star:nth-child(-n+1), .card__stars--2 .stars__star:nth-child(-n+2), .card__stars--3 .stars__star:nth-child(-n+3), .card__stars--4 .stars__star:nth-child(-n+4), .card__stars--5 .stars__star:nth-child(-n+5) {
